import React, { Fragment } from 'react'
import { connect } from 'react-redux'
import './styles.css'
import { Sidepanel } from './sidepanel'
import { appendMessage } from './redux'

const Message = ({data}) => (
  <li className="sent">
    <img src="http://emilcarlsson.se/assets/mikeross.png" alt="" />
    <div className='username'>{data.from.username}:</div>
    <p>{data.body}</p>
  </li>
)

@connect(state => ({
  user: state.user,
  token: state.token,
  messages: state.messages,
  active_chat: state.active_chat,
}), {appendMessage})
export class Chat extends React.Component {
  constructor (props) {
    super(props)

    this.socket = null
    this.state = {
      text: '',
    }
  }

  componentDidMount = () => {
    const url = 'ws://localhost:8080/private/ws?itok=' + this.props.token
    this.socket = new WebSocket(url)
    this.socket.onmessage = this.handleMessage
  }

  handleMessage = (event) => {
    const message = JSON.parse(event.data)
    this.props.appendMessage(message)
  }

  componentWillUnmount () {
    this.socket.close()
  }

  onMessageChange = (e) => {
    this.setState({text: e.target.value})
  }

  sendMessage = () => {
    if (this.state.text === "") {
      return null
    }

    const data = JSON.stringify({
      from: this.props.user,
      to: this.props.active_chat,
      body: this.state.text,
    })

    this.socket.send(data)
    this.setState({text: ''})
  }

  render = () => (
    <div id="frame">
      <Sidepanel user={this.props.user} />
      <div className="content">
        {
          this.props.active_chat &&
          <Fragment>
            <div className="contact-profile">
              <img src="http://emilcarlsson.se/assets/harveyspecter.png" alt="" />
              <p>{this.props.active_chat.username}</p>
            </div>

            <div className="messages">
              <ul>
                {this.props.messages
                     .filter(message => (
                       (message.from.id === this.props.active_chat.id) ||
                       (message.from.id === this.props.user.id && message.to.id === this.props.active_chat.id)
                     ))
                     .map(message => <Message data={message} />)}
              </ul>
            </div>

            <div className="message-input">
              <div className="wrap">
                <input placeholder="Write your message..." value={this.state.text} onChange={this.onMessageChange} />
                <button className="submit" onClick={this.sendMessage}>
                  <i className="fa fa-paper-plane" aria-hidden="true" />
                </button>
              </div>
            </div>
          </Fragment>
        }
      </div>
    </div>
  )
}



