import React, { Fragment } from 'react'
import { fetchContacts, setActiveChat } from './redux'
import { connect } from 'react-redux'
import { Popup } from '../uikit/popup'
import { RenameButton } from '../user/rename'

const Profile = ({user}) => (
  <div id="profile">
    <div className="wrap">
      <img id="profile-img" src="http://emilcarlsson.se/assets/mikeross.png"
           className="online" alt="" />
      <p>
        {user.username} <RenameButton user={user}/>
      </p>
    </div>
  </div>
)

@connect(state => ({
  user: state.user,
  contacts: state.contacts,
  active_chat: state.active_chat,
  messages: state.messages,
}), {fetchContacts, setActiveChat})
export class Sidepanel extends React.Component {
  componentDidMount = () => {
    this.props.fetchContacts()
  }

  setActive = (user) => {
    this.props.setActiveChat(user)
  }

  render = () => (
    <div id="sidepanel">
      <Profile user={this.props.user} />
      <div id="contacts">
        <ul>
          {
            this.props.contacts
                .filter(contact => contact.id !== this.props.user.id)
                .map(contact => <Contact onClick={e => this.setActive(contact)} data={contact} />)
          }
        </ul>
      </div>
    </div>
  )
}

@connect(state => ({
  user: state.user,
  active_chat: state.active_chat,
}))
class Contact extends React.Component {
  render = () => {
    const {active_chat, data} = this.props
    return (
      <li className={`contact ${active_chat && active_chat.id === data.id ? 'active' : ''}`}
          onClick={this.props.onClick}>
        <div className="wrap">
          <span className="contact-status" />
          <img src="http://emilcarlsson.se/assets/charlesforstman.png" alt="" />
          <div className="meta"><p className="name">{data.username}</p></div>
        </div>
      </li>
    )
  }
}



