import React, { Component } from 'react'
import './App.css'
import { Security } from './components/user'
import { Chat } from './components/chat'
import { connect } from 'react-redux'

@connect(state => ({
  user: state.user,
  token: state.token,
}))
class App extends Component {
  render () {
    return (
      <div className="App">
        {this.props.user && this.props.token ? <Chat /> : <Security />}
        {/*<Chat user={{token: '', username: 'fortis'}} />*/}
      </div>
    )
  }
}

export default App



